					}
					this.made_card=0;
					this.init();
				}
			},
			//
			getdata(type,page){
				if(getAccessToken()){
					this.isLogin=false;
					this.$http.post({
						url: '/activity/list',
						data: {
							type:type,
							page_flag:1,
							per_page:20,
							current_page:page
						}
					}).then((res) => {
						console.log(res)
						if(res.items.length==0){
							this.datad=0
						}else{
							this.table = res.items
							this.pagenumber=res.paging.last_page;
							this.datad=1
						}
					}).catch((err) => {
						console.log(err)
						this.$message.error(err.msg);
					})
				}else{
					this.isLogin=true;
					localStorage.removeItem('userinfo');
					localStorage.removeItem('accessToken_test');
					clearAccessToken();
				}

			},
			//
			sublimepage(index){
				if(index==1){
					this.page--
					this.page<1 ? this.page=1 : this.getdata(2,this.page);
				}else if(index==2){
					this.page++
					this.page>this.pagenumber ? this.page=this.pagenumber : this.getdata(2,this.page)
				}else if(index==3){
					this.page>this.pagenumber ? this.$message.error('') : this.getdata(2,this.page)
				}
